import React from 'react';
import { Navbar, Nav} from 'react-bootstrap';

const navbar = (props) => {
  const navLinks = [
    {
      site: "About",
      // href: "/#About"
      href: "/chelseajan-website/aboutme"

    },
    {
      site: "Work",
      href: "/chelseajan-website/#Work"
    },
    {
      site: "Uses",
      href: "/chelseajan-website/uses"
    },
    {
      site: "Portfolio",
      href: "/chelseajan-website/portfolio"
    },
    {
      site: "Contact",
      href: "/chelseajan-website/#Contact"
    }
  ]

  return (
  <Navbar bg="light" expand="lg">
    <div className="container">
      <Navbar.Brand href="/">{props.name}</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          {
            navLinks.map(data => {
              return (
                <Nav.Link href={data.href}>{data.site}</Nav.Link>
              )
            })
          }
        </Nav>
      </Navbar.Collapse>
    </div>
  </Navbar>
  )
}

export default navbar

